import { Component, OnInit, NgZone } from '@angular/core';
import { Router } from '@angular/router';
import { FormBuilder, FormGroup, Validators, FormControl } from '@angular/forms';
import { BaseApiService } from "../shared/api-service/base-api.service";
import { AuthenticationService } from "../shared/auth/index"
import { DialogService } from "../shared/dialog-service/dialog.service";

@Component({
  selector: 'app-signin',
  templateUrl: './signin.component.html',
  providers: [BaseApiService, DialogService, AuthenticationService],
  styleUrls: ['./signin.component.scss']
})
export class SigninComponent implements OnInit {

  returnUrl: string;
  public form: FormGroup;
  constructor(private dialogsService: DialogService, private authenticationService: AuthenticationService, private apiService: BaseApiService, private fb: FormBuilder, private router: Router, private zone: NgZone) { }

  public ngOnInit() {
    // reset login status
    //this.authenticationService.logout();

    this.form = this.fb.group({
      uname: [null, Validators.compose([Validators.required])], password: [null, Validators.compose([Validators.required])]
    });
  }

  onSubmit() {

    let isAuth: boolean = this.authenticationService.login(this.form.controls.uname.value, this.form.controls.password.value);

    console.log(isAuth);

    if (isAuth) {
      console.log("go to groups");
      this.router.navigate(['/groups']);
    } else {
      this.dialogsService.alert('Wrong username/pass ', 'The username/pass you entered is wrong');
    }


    // this.apiService.post("_session", {
    //     name : this.form.controls.uname.value,
    //     password : this.form.controls.password.value
    // }).subscribe(response => {
    //     console.log('Response' + response);
    //     alert("Horray");
    //     this.router.navigate ( [ '/dashboard' ] );
    // }, error => {
    //     console.log('Response' + error);
    //     alert("Wrong username / pass");
    // });
  }

}



